import get from 'lodash/get'
import PropTypes from 'prop-types'

import * as bindPropTypes from './bindPropTypes'
import BindValue from './BindValue'
import styles from '../Keys/styles.module.css'

function BindParamlist(props) {
  const { index, params, values, onSelect, root, wrapperProps } = props
  return (
    <span
      className={styles.params}
      data-is-root={!!root}
      data-param-count={params.length}
      {...wrapperProps}
    >
      {params.map((param, i) => (
        <span key={`param-${i}`} className={styles.param}>
          <BindValue
            index={index.indexOf(values[i])}
            param={param}
            value={get(values[i], 'value')}
            source={get(values[i], 'source')}
            onSelect={onSelect}
          />
          {get(values[i], 'source.params.length') > 0 ? (
            <BindParamlist
              index={index}
              params={get(values[i], 'source.params')}
              values={get(values[i], 'params')}
              onSelect={onSelect}
            />
          ) : null}
        </span>
      ))}
    </span>
  )
}

BindParamlist.propTypes = {
  index: bindPropTypes.index.isRequired,
  params: PropTypes.arrayOf(bindPropTypes.param).isRequired,
  values: PropTypes.arrayOf(bindPropTypes.node).isRequired,
  source: bindPropTypes.source,
  onSelect: PropTypes.func.isRequired
}

export default BindParamlist
