import { pick } from 'lodash'
import PropTypes from 'prop-types'
import { useMemo } from 'react'

import Modal from '../Common/Modal'
import EnumPicker from './EnumPicker'

import GenericPicker from './GenericPicker'
import RawValuePicker from './RawValuePicker'

function ValuePicker (props) {
  const { onCancel, onSelect } = props

  const pickerProps = pick(props, [
    'value', 'prompt', 'param', 'behaviour', 'context', 'searchKey'
  ])

  const PickerComponent = useMemo(() => {
    if (props.param.raw) return RawValuePicker
    if (props.param.enum) return EnumPicker
    return GenericPicker
  }, [props.param])

  return (
    <Modal onDismiss={onCancel}>
      <PickerComponent
        {...pickerProps}
        onSelect={onSelect}
      />
    </Modal>
  )
}

ValuePicker.propTypes = {
  value: PropTypes.any.isRequired,
  behaviour: PropTypes.object.isRequired,
  prompt: PropTypes.string.isRequired,
  searchKey: PropTypes.string.isRequired,
  searchThreshold: PropTypes.number,
  onCancel: PropTypes.func.isRequired,
  onSelect: PropTypes.func.isRequired
}

ValuePicker.defaultProps = {
  value: '',
  freeForm: false,
  searchThreshold: 10
}

export default ValuePicker
