import { useCallback, useMemo, useState } from 'react'

import Context from './Context'
import ComboEditor from './Combos/ComboEditor'
import KeyboardLayout from './KeyboardLayout'
import LayerSelector from './LayerSelector'
import * as layoutPropTypes from './layoutPropTypes'
import { getWrapperStyle } from './util'
import { useAppState } from '../contexts/AppState'
import Key from './Keys/Key'
import styles from './styles.module.css'
import MacroEditor from './Macros/MacroEditor'
import SensorBindings from './SensorBindings'

function Tabs ({ text, tabs, children }) {
  const [activeTab, setActiveTab] = useState(0)

  return (
    <>
      <h3 style={{ display: 'inline' }}>{text}</h3>
      <ul className={styles.editorModeTabs}>
        {tabs.map((tab, i) => (
          <li
            key={i}
            onClick={() => setActiveTab(i)}
            className={activeTab === i ? styles.activeEditTab : ''}
          >
            {tab}
          </li>
        ))}
      </ul>
      {children[activeTab]}
    </>
  )
}

function Keyboard(props) {
  const [keymap, dispatch] = useAppState('keyboard.keymap')
  const { layout, sensors } = props
  const [activeLayer, setActiveLayer] = useState(0)
  const layer = Math.max(0, Math.min(activeLayer, keymap.layers.length - 1))

  const wrapperStyle = useMemo(() => getWrapperStyle(layout), [layout])

  const handleCreateLayer = useCallback(function () {
    dispatch({ type: 'CREATE_LAYER', bindingCount: layout.length })
  }, [layout, dispatch])

  const handleRenameLayer = useCallback(function (index, name) {
    dispatch({ type: 'RENAME_LAYER', index, name })
  }, [dispatch])

  const handleDeleteLayer = useCallback(function (index) {
    dispatch({ type: 'DELETE_LAYER', index })
  }, [dispatch])

  const handleUpdateBind = useCallback(function(bindIndex, binding) {
    dispatch({ type: 'UPDATE_KEY_BINDING', layerIndex: activeLayer, bindIndex, binding })
  }, [dispatch, activeLayer])

  const handleUpdateSensorBindings = useCallback(function (sensorIndex, bindings) {
    dispatch({ type: 'UPDATE_SENSOR_BINDINGS', layerIndex: layer, sensorIndex, bindings })
  }, [dispatch, layer])

  return (
    <Context keymap={keymap}>
      <Tabs text="Editor mode:" tabs={['keymap', 'combos', 'macros']}>
        <>
          <LayerSelector
            layers={keymap.layer_names}
            activeLayer={layer}
            onSelect={setActiveLayer}
            onNewLayer={handleCreateLayer}
            onRenameLayer={name => handleRenameLayer(activeLayer, name)}
            onDeleteLayer={handleDeleteLayer}
          />
          <div style={wrapperStyle}>
            <h3>Key Bindings</h3>
            <KeyboardLayout
              layout={layout}
              bindings={keymap.layers[layer].bindings}
              renderKey={({ index, keyLayout }) => keymap.layers[layer].bindings[index] && (
                <Key
                  label={keyLayout.label}
                  highlight={false}
                  value={keymap.layers[layer].bindings[index]?.value}
                  params={keymap.layers[layer].bindings[index]?.params}
                  onUpdate={bind => handleUpdateBind(index, bind)}
                />
              )}
            />
          </div>
          {sensors.length > 0 && (
            <div style={{
              width: wrapperStyle.width,
              margin: wrapperStyle.margin,
              padding: wrapperStyle.padding
            }}>
              <SensorBindings
                sensors={sensors}
                layer={layer}
                onUpdate={handleUpdateSensorBindings}
              />
            </div>
          )}
        </>

        <ComboEditor layout={layout} />

        <MacroEditor />
      </Tabs>
    </Context>
  )
}

Keyboard.propTypes = {
  layout: layoutPropTypes.layout.isRequired
}

export default Keyboard
