import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'

const styles = {
  description: {
    fontStyle: 'italic'
  }
}

function Settings (props) {
  const { settings, onUpdate, onClose } = props

  function handleUpdateSetting (key, value) {
    onUpdate({ ...settings, [key]: value })
  }

  return (
    <Modal onDismiss={onClose}>
      <DialogBox onDismiss={onClose}>
        <h1>Settings</h1>
        <h2>Experimental</h2>

        <h3>
          <label htmlFor="enable-legacy-keymap">
            <input
              id="enable-legacy-keymap"
              type="checkbox"
              checked={settings.legacyKeymap || false}
              onChange={e => (
                handleUpdateSetting('legacyKeymap', e.target.checked)
              )}
            />
            Enable Legacy Keymap (JSON)
          </label>
        </h3>
        <p style={styles.description}>
          Use <code>keymap.json</code> file instad of editing the devicetree
          keymap directly. This feature prevents the use of combo and macro
          editing, and causes all customizations in the keymap file to be erased
          each time changes are saved. This will eventually be removd.
        </p>
      </DialogBox>
    </Modal>
  )
}

Settings.propTypes = {
  settings: PropTypes.object.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}

Settings.defaultProps = {
  settings: {}
}

export default Settings
