import { useEffect, useMemo, useState } from 'react'
import PropTypes from 'prop-types'

import GithubPicker from './Github/Picker'
import { sourceChoices, defaultSource } from './sources'
import styles from './style.module.css'

import { loadKeyboard } from '../api'
import IconButton from '../Common/IconButton'
import Selector from '../Common/Selector'
import DemoHelpDialog from '../Dialogs/DemoHelpDialog'

function KeyboardPicker(props) {
  const { onSelect } = props
  const [source, setSource] = useState(defaultSource)
  const [showDemoHelpDialog, setShowDemoHelpDialog] = useState(false)

  const handleKeyboardSelected = useMemo(() => function (event) {
    const { layout, keymap, sensors, ...rest } = event

    const layerNames = keymap.layer_names || keymap.layers.map((_, i) => `Layer ${i}`)
    Object.assign(keymap, {
      layer_names: layerNames
    })

    onSelect({ source, layout, keymap, sensors, ...rest })
  }, [onSelect, source])

  const fetchLocalKeyboard = useMemo(() => async function() {
    const { layout, keymap, sensors } = await loadKeyboard()

    handleKeyboardSelected({ source, layout, keymap, sensors })
  }, [source, handleKeyboardSelected])

  const fetchDemoKeyboard = useMemo(() => async function () {
    const { layout, keymap } = await import('../data/demo.json')
    handleKeyboardSelected({ source, layout, keymap })
  }, [source, handleKeyboardSelected])

  useEffect(() => {
    localStorage.setItem('selectedSource', source)
    if (source === 'local') {
      fetchLocalKeyboard()
    } else if (source === 'demo') {
      fetchDemoKeyboard()
    }
  }, [source, fetchLocalKeyboard, fetchDemoKeyboard])

  return (
    <div>
      <Selector
        id="source"
        label="Source"
        value={source}
        choices={sourceChoices}
        onUpdate={setSource}
      />

      {source === 'demo' && (
        <IconButton
          icon="info"
          className={styles.help}
          onClick={() => setShowDemoHelpDialog(true)}
        />
      )}

      {showDemoHelpDialog && (
        <DemoHelpDialog onDismiss={() => setShowDemoHelpDialog(false)} />
      )}

      {source === 'github' && (
        <GithubPicker onSelect={handleKeyboardSelected} />
      )}
    </div>
  )
}

KeyboardPicker.propTypes = {
  onSelect: PropTypes.func.isRequired
}

export default KeyboardPicker
