import fuzzysort from 'fuzzysort'
import PropTypes from 'prop-types'
import { useCallback, useMemo,  useState } from 'react'

const searchThreshold = 10

function focusInput (node) {
  if (node) {
    node.focus()
    node.select()
  }
}

const SearchFilter = ({ choices, searchKey, renderResults, initialQuery }) => {
  const useSearch = !choices.length || choices.length > searchThreshold
  const [query, setQuery] = useState(initialQuery)
  const results = useMemo(() => {
    if (!query || !useSearch) {
      return choices
    }

    const options = { key: searchKey, limit: 30 }
    const filtered = fuzzysort.go(query, choices, options)
    return filtered.map(result => ({
      ...result.obj,
      search: result
    }))
  }, [useSearch, query, choices, searchKey])

  const handleKeyPress = useCallback(event => {
    setQuery(event.target.value)
  }, [setQuery])

  return (
    <>
      {useSearch && (
        <input
          ref={focusInput}
          type="text"
          value={query}
          onChange={handleKeyPress}
        />
      )}
      {renderResults(results)}
    </>
  )
}

SearchFilter.propTypes = {
  choices: PropTypes.array.isRequired,
  searchKey: PropTypes.string.isRequired,
  initialQuery: PropTypes.string.isRequired,
  renderResults: PropTypes.func.isRequired
}

SearchFilter.defaultProps = {
  initialQuery: ''
}

export default SearchFilter
