import compact from 'lodash/compact'
import { useMemo } from 'react'
import PropTypes from 'prop-types'

import * as bindPropTypes from './bindPropTypes'
import styles from '../Keys/styles.module.css'
import Icon from '../../Common/Icon'

function NullKey() {
  return <span>⦸</span>
}

function BindValue(props) {
  const { param, index, value, source, onSelect } = props
  const title = source && `(${source.code}) ${source.description}`
  const text = source && (source?.symbol || source?.code.toString())
  const icon = source?.faIcon && <Icon name={source.faIcon} />

  const handleClick = useMemo(() => function (event) {
    event.stopPropagation()
    onSelect({
      codeIndex: index,
      code: value,
      param
    })
  }, [param, value, index, onSelect])

  return (
    <span
      className={compact([
        styles.code,
        value === undefined && styles.missingValue
      ]).join(' ')}
      title={title}
      onClick={handleClick}
    >
      {icon || text || <NullKey />}
    </span>
  )
}

BindValue.propTypes = {
  index: PropTypes.number.isRequired,
  param: bindPropTypes.param.isRequired,
  value: bindPropTypes.value,
  source: bindPropTypes.source,
  onSelect: PropTypes.func.isRequired
}

export default BindValue
